<template>
    <div class="menu" :class="{'closeMenu': menuOpen}">
        <router-link to = '/member/info' class="edit"><i class="el-icon-s-tools" /></router-link>
        <i class="el-icon-arrow-left switch-btn" :class="{'close': menuOpen}" @click="menuClose()"/>
        <div class="menu-account">
            <div class="head">
                <i v-if="avatar === ''" class="el-icon-user-solid" />
                <div v-else class="avatar">
                    <img :src="avatar" alt="">
                </div>
                <i @click="uploadDiglog" class="el-icon-camera-solid"/>
            </div>
            <p class="name">王曉明</p>
            <p class="company">一二三股份有限公司</p>
            <p class="sub-company">(統編:123456789)</p>
        </div>
        <div class="menu-btn">
            <router-link to = '/member/favorite' :class="{ 'active' : favorite.includes(openMenu)}" >
                <img src="~@/assets/img/member/favorite.png" alt="" srcset="">
                <p class="num">98</p>
                瀏覽與收藏
            </router-link>
            <span class="line"/>
            <router-link to = '/member/message' :class="{ 'active' : message.includes(openMenu)}" >
                <img src="~@/assets/img/member/conversation.png" alt="" srcset="">
                <p class="num">1,100</p>
                留言管理
            </router-link>
            <span class="line"/>
            <router-link to = '/member/postCar' :class="{ 'active' : postCar.includes(openMenu)}">
                <img src="~@/assets/img/member/caricon.png" alt="" srcset="">
                <p class="num">25</p>
                刊登/管理車輛
            </router-link>
        </div>
        <div class="menu-info"> 
            <router-link :to ='`/member/${item.path}`'  v-for="item in menu[openMenu]" :key="item.path" :class="{'active' : item.path === page}">
                <p>{{item.name}}</p>
                <i class="el-icon-arrow-left" />
            </router-link>
            
        </div>
        <el-dialog title="上傳頭像" :visible.sync="showupload">
            <Upload @uploadDiglog="uploadDiglog"></Upload>
        </el-dialog>
    </div>
</template>

<script>
import Upload from "../upload";
export default {
    name:'memberMenu',
    components:{
        Upload
    },
    data() {
        return {
            avatar:"",
            showupload: false,
            menuOpen: false,
            menu: {
                favorite : [
                    {
                        name:'我的收藏',
                        path: 'favorite',
                    },
                    {
                        name:'瀏覽歷史',
                        path: 'browseRecord',
                    },
                ],
                message : [
                    {
                        name:'全部留言',
                        path: 'message',
                    },
                    {
                        name:'待處理留言 (99+)',
                        path: 'unreadMessage',
                    },
                ],
                postCar: [
                    {
                        name:'實名制認證',
                        path: 'certification',
                    },
                    {
                        name:'刊登車輛資訊',
                        path: 'postCar',
                    },
                    {
                        name:'管理我的車輛',
                        path: 'myCar',
                    },
                    {
                        name:'誰收藏我',
                        path: 'favoriteMe',
                    },
                    {
                        name:'誰看過我',
                        path: 'lookMe',
                    },
                ]
            },
            favorite:['favorite','browseRecord'],
            message: ['message','unreadMessage'],
            postCar: ['postCar','certification','myCar','favoriteMe','lookMe'],
        }
    },
    computed: {
        openMenu() {
            if(this.favorite.includes(this.$route.name)){
                return 'favorite'
            }else if(this.message.includes(this.$route.name)){
                return 'message'
            }else if(this.postCar.includes(this.$route.name)){
                return 'postCar'
            }
        },
        page() {
            return this.$route.name
        },
    },
    methods: {  
        uploadDiglog(img){
            if(typeof img === "string") this.avatar = img;
            this.showupload = !this.showupload;
        },
        menuClose() {
            this.menuOpen = !this.menuOpen
        }
    }
}
</script>

<style lang="scss" scoped>
.menu {
    background: #f5f5f5;
    width: 475px;
    height: 100%;
    position: relative;
    padding: 20px 0;
    box-sizing: border-box;
    left: 0;
    transition: all 0.3s;
    z-index: 10;
    .edit {
        position: absolute;
        right: 20px;
        top: 20px;
        font-size: 30px;
        color: #ef6d38;
    }
    .switch-btn {
        position: absolute;
        top: 0;
        right: -26px;
        color: #f5f5f5;
        background: #555555;
        border-radius: 0px 10px 10px 0px;
        padding: 20px 5px;
        cursor: pointer;
        &.close {
            transform: rotate(180deg);
            border-radius: 10px 0px 0px 10px;
        }
    }
    .menu-account {
        padding: 50px 0;
        text-align: center;
        color: #555555;  
        .head {
            background: #fff;
            border: 2px solid #ef6d38;
            width: 150px;
            height: 150px;
            border-radius: 75px;
            text-align: center;
            position: relative;
            margin: auto;
            .avatar{
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 100%;
                img{
                    max-width: 100%;
                    height: auto;
                }
            }
            .el-icon-user-solid {
                color: #ef6d38;
                font-size: 120px;
                line-height: 150px;
            }
            .el-icon-camera-solid {
                background: #ef6d38;
                color: #fff;
                position: absolute;
                bottom: 10px;
                right: -5px;
                font-size: 30px;
                padding: 5px;
                border-radius: 20px;
                border: 2px solid #fff;
                cursor: pointer;
            }
        }
        .name {
            margin: 35px 0;
            font-size: 30px;
            font-weight: bold;
        }
        .company {
            color: #ef6d38;
            font-size: 22px;
            font-weight: bold;
        }
    }
    .menu-btn {
        display: flex;
        border-right: 8px solid #ef6d38;
        color: #555555;
        a {
            width: 33%;
            text-align: center;
            padding: 20px 0;
        }
        .num {
                color: #ef6d38;
                margin: 10px 0;
                font-size: 30px;
                font-weight: bold;
            }
        .line {
            width: 2px;
            display: block;
            background: linear-gradient(to bottom, transparent 0%,#ccc 50%,transparent 100%);
        }
        .active {
            background: linear-gradient(to bottom, transparent 0%,rgb(255, 255, 255) 80%,transparent 100%);
            position: relative;
            ::after {
                content: "〉";
                position: absolute;
                bottom: -35px;
                right: calc(50% - 20px);
                transform: rotate(90deg);

            }
        }
    }
    .menu-info {
        margin: 40px 0;
        a {
            width: 80%;
            background:linear-gradient(125deg, #fff 80%, #555555 0) bottom right;
            display: block;
            margin: 20px auto;
            padding: 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            box-shadow: 0px 3px 7px -4px #4C4C4C;
            
            p {
                padding-left: 20px;
                border-left: #ef6d38 5px solid;
            }
            i {
                color: #fff;
            }
        }
        .active {
            background:linear-gradient(125deg, #fff 80%, #ef6d38 0) bottom right;
        }
    }
    @media screen and (max-width: 768px){
        width: 100%;
        .menu-btn {
            border: none;
        }
        .switch-btn {
            display: none;
        }
    }
}
.closeMenu {
    left: -475px;
    position: fixed;
    top: 405px;
    z-index: 2;
    @media screen and (max-width: 768px){ 
        left: 0;
    }
}


</style>


