<template>
    <div class="member">
        <div class="member-title">
            <p class="sub-title">會員中心</p>
            <p class="title">MEMBER CENTER</p>
        </div>
        
        <div class="member-content">
            <div class="member-menu">
                <MemberMenu/>
            </div>
            <component :is="page" />
        </div>

    </div>
</template>

<script>
import MemberMenu from './meun'
import info from './info'
import favorite from './favorite'
import favoriteMe from './favoriteMe'
import browseRecord from './browseRecord'
import lookMe from './lookMe'
import message from './message'
import unreadMessage from './unreadMessage'
import postCar from './postCar'
import certification from './certification'
import myCar from './myCar'
export default {
    name:'member',
    components: {
        MemberMenu,
        info,
        favorite,
        favoriteMe,
        browseRecord,
        message,
        unreadMessage,
        lookMe,
        postCar,
        certification,
        myCar,
    },
    data() {
        return {
        }
    },
    computed: {
        page() {
            return this.$route.name
        } 
    }


}
</script>

<style lang='scss' scoped>
.member {
    padding: 105px 0 0 0;
    .member-title {
        background: url('~@/assets/img/member/member-banner.png') no-repeat center;
        background-size: cover;
    }
    .member-content {
        display: flex;
        @media screen and (max-width: 768px){  
            display: block;
        }
    }

}

</style>


